import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import classNames from "classnames"
import Icon from "./icon"

import "../css/layout.css"
import "../css/header.css"

const Header = ({ siteTitle }) => (
  <header className={classNames( 'siteContrast' )}>
    <div className={classNames( 'siteWidth', 'headerWidth' )}>
      <Link className={classNames( 'icon' )} to='/' >
        <Icon />
      </Link>
      <div className={classNames( 'column', 'maxW' )}>
        <div className={classNames( 'row' )}>
          <div className={classNames( 'flex' )} />
          <div className={classNames( 'column' )}>
            <div className={classNames( 'flex' )} />
            <h className={classNames( 'siteTitle' )}>
              <Link className={classNames( 'light' )} to="/">
                {siteTitle}
              </Link>
            </h>
            <div className={classNames( 'flex' )} />
          </div>
          <div className={classNames( 'flex' )} />
          <h className={classNames( 'siteDescription', 'light' )}>
            Der Blog von Karl-Katja Krach
          </h>
          <div className={classNames( 'flex' )} />
        </div>
        <div className={classNames( 'row', 'menuRow' )}>
          <h className={classNames( 'menuEntry', 'light' )}>
            Blog
          </h>
          <h className={classNames( 'menuEntry', 'light' )}>
            Lesen
          </h>
          <h className={classNames( 'menuEntry', 'light' )}>
            Hören
          </h>
          <h className={classNames( 'menuEntry', 'light' )}>
            Leistungen
          </h>
          <h className={classNames( 'menuEntry', 'light', 'about' )}>
            K.-K. Krach
          </h>
        </div>
      </div>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: '',
}

export default Header
